.footer {
  background-color: #222222;
  color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: 600;
  font-size: 20px;
  padding: 0 1em;
}

.left a {
  color: #ffffff;
  text-decoration: none;
  margin: 0.6em 0;
}

.footer a:hover {
  text-decoration: underline;
}

.logo {
  padding: 0 5em;
}

.left {
  display: flex;
  flex-direction: row;
}

.phone {
  display: flex;
  flex-direction: column;
  align-items: end;
  margin-right: 5em;
  justify-content: center;
}

.phone > * + * {
  margin: 0.4em;
}

.messengers {
  display: flex;
  flex-direction: row;
}

.messengers a {
  color: #ffffff;
  text-decoration: none;
  margin:  0 0.5em;
}

.messengers a:hover {
  text-decoration: none;
  cursor: pointer;
}
